import { firestore } from "@/firebase";
import {
    getFirestore,
    collection,
    where,
    query,
    getDocs,
    updateDoc,
    deleteDoc,    
} from "firebase/firestore";

export default {
    state: {
        mealList: null,
    },
    mutations: {
        fetchMealdayList(state, mealList) {
            state.mealList = mealList;
        },        
    },
    actions: {
        
        async fetchMealdayList({ commit }) {            
            let mealArray = [];
            const queryRef = query(
                collection(getFirestore(), "meal")
            );
            await firestore
                .getDocs(queryRef)
                .then((response) => {
                    response.forEach((element) => {
                        const elementData = element.data();                        
                        const index = mealArray.indexOf(elementData.id);
                        // console.log('elementData ', elementData);
                        if (index === -1) {
                            mealArray.push(elementData);
                        }
                    });
                    commit("fetchMealdayList", mealArray);
                })
                .catch(() => {
                throw Error("Error due fetch mealList.");
            });       
        },

        async addNewMeal({ rootState, dispatch }, newMeal) {
            const userUid = rootState.auth.user.uid;            
            const collectionRef = collection(getFirestore(), "meal");
            newMeal.uid = userUid;
            await firestore
                .addDoc(collectionRef, newMeal)
                .then(() => dispatch("fetchMealdayList"))
                .catch(() => {
                throw Error("Error due add new Meal.");
                });            
        },        

        async deleteMeal({ dispatch }, dayid) {
            const queryRef = query(
                collection(getFirestore(), "meal"),
                where("id", "==", dayid),
            );
            const querySn = await getDocs(queryRef);
            if ( querySn.docs.length !==0 ){
                await deleteDoc(querySn.docs[0].ref)
                        .then(() => dispatch("fetchMealdayList"))
                        .catch(() => {
                            throw Error("Error due delete Meal.");  
                        });
            }            
        },
        
        async updateMeal({ dispatch }, newMeal) {
            const newDoc= { 
                meal : newMeal.meal,
                created_at: new Date(),
            };
            const queryRef = query(
                collection(getFirestore(), "meal"),
                where("id", "==", newMeal.id),
            );
            const querySn = await getDocs(queryRef);
            if ( querySn.docs.length !==0 ){                
                await updateDoc(querySn.docs[0].ref, newDoc)
                    .then(() => dispatch("fetchMealdayList"))
                    .catch(() => {
                            throw Error("Error due delete Meal.");  
                    });
            }
        },
    },
};