import router from "@/router";
import { app, auth, firestore} from "@/firebase";
import { getFirestore, setDoc, getDoc, getDocs, query, doc, deleteDoc, updateDoc} from "firebase/firestore"

export default {
    state: {
        user: null,
        userList:null,
    },
    getters: {
        isAuthenticated(state) {
        return !!state.user;
        },
    },
    mutations: {
        signUp() {
            router.push("/sign-in");
        },
        signIn(state, user) {
            state.user = user;
            router.push("/home");
        },
        signOut(state) {
            state.user = {};
            router.push("/");
        },
        fetchAlluserList(state, userList) {
            state.userList = userList;
        },
    },
    actions: {
        
        async signUpWithEmailAndPassword({ commit }, { email, password, username, telnum }) {
        await auth
            .createUserWithEmailAndPassword(auth.getAuth(app), email, password)
            .then((response) => {
            auth
                .sendEmailVerification(response.user)
                .then(() => commit("signUp"));
                try {
                    var db = getFirestore(app);
                    const USER_COLLECTION= firestore.collection(db,"users");
                    const docRef = doc(USER_COLLECTION, response.user.uid);
                    setDoc(docRef, {
                        uid: response.user.uid,
                        email: response.user.email,
                        username: username,
                        authority: false,
                        telnum: telnum,
                        create_at: Date.now(),
                    })
                } catch(error){
                    console.log(error);
                }            
            })
            .catch(() => {
            throw Error("Email is already registered.");
            });
        },
        
        async signInWithEmailAndPassword({ commit }, { email, password }) {
        await auth
            .signInWithEmailAndPassword(auth.getAuth(app), email, password)
            .then((response) => {
            if (response.user.emailVerified) {

                try {
                    var db = getFirestore(app);
                    const docRef= doc(db, "users", response.user.uid);
                    getDoc(docRef).then(docSnap => {
                        if(docSnap.exists()) {
                            commit("signIn", docSnap.data());
                        } else {
                            console.log("No such document");                            
                        }
                    })
                }catch(error) {
                    console.log(error);
                }
                commit("signIn", response.user);
            } else {
                throw Error("Email isn't verified.");
            }
            })
            .catch((error) => {
            if (error.message === "Email isn't verified.") {
                throw Error("Email isn't verified.");
            }
            throw Error("Invalid email or password.");
            });
        },

        async sendResetPassword({commit}, { email }) {            
        await auth
            .sendPasswordResetEmail(auth.getAuth(app), email)
            .then(() => {
                console.log("good sent to"+email );
            })            
            .catch((error) => {
                console.log(error);
            });
            commit("signIn", '');
        },
        
        async signInWithGoogle({ commit }) {
        await auth
            .signInWithPopup(auth.getAuth(app), new auth.GoogleAuthProvider())
            .then((response) => {
            commit("signIn", response.user);
            });
        },
        
        async signOut({ commit }) {
        await auth
            .signOut(auth.getAuth(app))
            .then(() => commit("signOut"))
            .catch((error) => {
                console.log(error);
            });
        },

        async fetchAlluserList( { commit} ) {
            let userArray = [];
            try {                
                var db = getFirestore(app);
                const q= query(firestore.collection(db,"users"));
                const querySnapshot = await getDocs(q);                   
                querySnapshot.forEach( (doc) => {
                    userArray.push(doc.data());
                });
                if(userArray.length>0) {
                    commit("fetchAlluserList", userArray);
                }
            } catch(error){
                console.log(error);
            } 
        },

        async fireBaseDeleteUser({ dispatch }, user) {
            
            auth.deleteUser(user).then( () =>{
                console.log('good delete user');
            }).catch((error) => {
                console.log(error);
            });
            
            try {
                var db = getFirestore(app);
                const docRef= doc(db, "users", user.uid);                                
                console.log(docRef);
                deleteDoc(docRef)
                .then(() => dispatch("fetchAlluserList"))
                .catch(() =>{
                    throw Error("Error due delete User.");
                });                
            } catch(error) {
                console.log(error);
            }                       
        },

        async userAuthrity({ dispatch }, user) {
            try {
                var db = getFirestore(app);
                const docRef= doc(db, "users", user.uid);
                
                updateDoc(docRef, { authority: user.authority})
                    .then(() => dispatch("fetchAlluserList"))
                    .catch(() =>{
                        throw Error("Error due userAuthriy.");
                    });                
            } catch(error) {
                console.log(error);
            }
        }
    },
};