import { createRouter, createWebHistory } from "vue-router";
import store from "@/store";

const routes = [
    {
        path: "/",
        component: () => import("@/views/SignInView"),
    },
    {
        path: "/sign-in",
        name: "sign-in",
        component: () => import("@/views/SignInView"),
    },
    {
        path: "/sign-up",
        name: "sign-up",
        component: () => import("@/views/SignUpView"),
    },
    {
        path: "/home",
        name: "home",
        component: () => import("@/views/HomeView"),
        meta: { requiresAuth: true },
    },
    {
        path: "/ticketcount",
        name: "thicktcount",
        component: () => import("@/views/TicketCountView"),
        meta: { requiresAuth: true },
    },
    {
        path: "/useradmin",
        name: "useradmin",
        component: () => import("@/views/UserAdminView"),
        meta: { requiresAuth: true },
    },
    {
        path: "/holiday",
        name: "holiday",
        component: () => import("@/views/HolidayView"),
        meta: { requiresAuth: true },
    },
    {
        path: "/menu",
        name: "menu",
        component: () => import("@/views/MealView"),
        meta: { requiresAuth: true },
    },
    {
        path: "/userinfo",
        name: "userinfo",
        component: () => import("@/views/UserInfoView"),
        meta: { requiresAuth: true },
    },
    {
        path: "/passwd",
        name: "passwd",
        component: () => import("@/views/PasswordChangeView"),
    },
    {
        path: "/dayticket",
        name: "dayticket",
        component: () => import("@/views/DayTicketView"),
    },
    {
        path: "/backupdelete",
        name: "backupdelete",
        component: () => import("@/views/BackupDeleteView"),
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

router.beforeEach(async (to, from, next) => {
    if (to.matched.some((record) => record.meta.requiresAuth)) {
        if (store.getters.isAuthenticated) {
        next();
        return;
        }
        next("/");
    } else {
        next();
    }
});

export default router;