import { firestore } from "@/firebase";
import {
    getFirestore,
    collection,
    where,
    query,
    getDocs,
    deleteDoc,
} from "firebase/firestore";

export default {
    state: {
        holidayList: null,
    },
    mutations: {
        fetchHolidayList(state, holidayList) {
            state.holidayList = holidayList;
        },        
    },

    actions: {
        
        async fetchHolidayList({ commit }) {            
            let holidayArray = [];
            const queryRef = query(
                collection(getFirestore(), "holidays")
            );
            await firestore
                .getDocs(queryRef)
                .then((response) => {
                    response.forEach((element) => {
                        const elementData = element.data();                        
                        const index = holidayArray.indexOf(elementData.id);
                        if (index === -1) {
                            holidayArray.push(elementData);
                        }
                    });
                    commit("fetchHolidayList", holidayArray);
                })
                .catch(() => {
                throw Error("Error due fetch HolidayList.");
            });       
        },

        async addNewHoliday({ rootState, dispatch }, newHoliday) {
            const userUid = rootState.auth.user.uid;            
            const collectionRef = collection(getFirestore(), "holidays");
            newHoliday.uid = userUid;
            await firestore
                .addDoc(collectionRef, newHoliday)
                .then(() => dispatch("fetchHolidayList"))
                .catch(() => {
                throw Error("Error due add new Holidays.");
                });            
        },        

        async deleteHoliday({ dispatch }, dayid) {
            const queryRef = query(
                collection(getFirestore(), "holidays"),
                where("id", "==", dayid),
            );
            const querySn = await getDocs(queryRef);
            if ( querySn.docs.length !==0 ){
                await deleteDoc(querySn.docs[0].ref)
                        .then(() => dispatch("fetchHolidayList"))
                        .catch(() => {
                            throw Error("Error due delete Holiday.");  
                        });
            }            
        },        
    },
};